//React basic imports
import React, {Component} from 'react';

import Error from './Error';

export default class ErrorWrapper extends Component{
  constructor(props){
    super(props);

    this.state = {
      Error: false
    }
  }

  /*
    Catch a global error, to avoid the default React behavior of 
    unmount the whole app
  */
  componentDidCatch(error, info){
    console.log('Error Ocurred...');
    console.log('Error: ', error.toString());
    console.log('Error Info: ', info);

    this.setState({
      Error: {name: error.toString(), StackTrace: info.componentStack}
    });
  }

  render(){
    if(this.state.Error) return (<Error error={this.state.Error} changeState={this.ChangeState}/>);
      return this.props.children;
  }
}